*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Bold.306cbdd3.ttf"), url("DMSans-Bold.c13d45cb.woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Medium.b8d1a1bf.ttf"), url("DMSans-Medium.7771cc00.woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Regular.331f979e.ttf"), url("DMSans-Regular.576a6f0a.woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Regular-Italic.ed07c4fe.ttf"), url("DMSans-Regular-Italic.4f0a6d28.woff2");
  font-weight: 400;
  font-display: swap;
  font-style: italic;
}

:root {
  --font-main: "DM Sans";
  --light-body-color: #d0d0d0;
  --light-nav-text-color: #000;
  --light-header-bg-color: #fff;
  --light-border-n-text-color: #111;
  --light-accent-color: #4f2ee8;
  --light-accent-color-2: #eac645;
  --light-description-text: rgba(17, 17, 17, .6);
  --light-header-secondary-color: #b4afaf;
  --dark-body-color: #202024;
  --dark-nav-text-color: #f3f3f3;
  --dark-header-bg-color: #111;
  --dark-border-n-text-color: #fff;
  --dark-accent-color: #4f2ee8;
  --dark-accent-color-2: #eac645;
  --dark-description-text: rgba(255, 255, 255, .6);
  --dark-header-secondary-color: #b4afaf;
  --animation: 250ms cubic-bezier(.4, 0, .2, 1);
  --animation-slow: 450ms cubic-bezier(.4, 0, .2, 1);
}

body {
  min-height: 100vh;
  min-width: 100vw;
  font-family: var(--font-main);
  flex-direction: column;
  font-size: 18px;
  transition: background-color 1s cubic-bezier(.075, .82, .165, 1);
  display: flex;
  overflow-x: hidden;
}

main {
  flex-grow: 1;
}

footer {
  flex-shrink: 0;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.margin-top .support {
  margin-top: 0;
}

h1, h2, h3, h4, p, ul {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  padding-left: 0;
  list-style: none;
}

img {
  width: 100%;
  border-radius: 8px;
  display: block;
}

.hiden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

